import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';
//import { CartPage } from '../../cart/cart';
//import { ProductPage } from '../../product/product';
import { Values } from '../../../providers/service/values';
import { Functions } from '../../../providers/service/functions';
import { WishlistService } from '../../../providers/service/wishlist-service';


@IonicPage()
@Component({
  templateUrl: 'wishlist.html'
})
export class Wishlist {

  count: any;
  status: any;
  wishlist: any;

  constructor(public nav: NavController, public service: WishlistService, public values: Values, public functions: Functions) {

  	this.count = 10;
  	this.service.load()
    .then((results) => this.wishlist = results);
  }

  
  getCart(){
    this.nav.push('CartPage');
  }

  
  addToCart(product_id){
    this.service.addToCart(product_id)
    .then((results) => this.handleWishlist(results, product_id));
  }
  
  
  handleWishlist(results,id){
    if(results.success){
      this.functions.showAlert(this.values.lan.text_success, results.success);
    }
   
    else if(results.error){
      this.nav.push('ProductPage', results.redirect);
    }
  }


  delete(id){
    this.service.deleteItem(id)
    .then((results) => this.update(results));     
  }

  
  update(results){
    this.service.load()
    .then((results) => this.wishlist = results);
  }
  
}



